const setSearchTerm = (searchTerm) => function (dispatch) {
  return dispatch({
    type: 'SET_SEARCH_TERM',
    payload: searchTerm
  })
}

const setCommandName = (commandName) => function (dispatch) {
  return dispatch({
    type: 'SET_COMMAND_NAME',
    payload: commandName
  })
}

const setCommandHistory = (commandName) => function (dispatch) {
  return dispatch({
    type: 'SET_COMMAND_HISTORY',
    payload: commandName
  })
}

const setMobileNav = (isVisible) => function (dispatch) {
  return dispatch({
    type: 'SET_MOBILE_NAV',
    payload: isVisible
  })
}

export { setSearchTerm, setCommandName, setCommandHistory, setMobileNav }



