import React from 'react'
import { connect } from 'react-redux'
import { setSearchTerm, setCommandName, setCommandHistory, setMobileNav } from '../../actions/SearchActions'

// Styles
import './Nav.scss'

const { string, func, array, bool } = React.PropTypes

const Nav = React.createClass({
  propTypes: {
    searchTerm: string,
    commandHistory: array,
    mobileNavIsVisible: bool,
    setSearchTerm: func,
    setCommandName: func,
    setCommandHistory: func,
    setMobileNav: func
  },
  getInitialState () {
    return {
      'showMobileNav': false
    };
  },
  toggleMenu () {
    this.setState({
      showMobileNav: !this.state.showMobileNav
    })
  },
  render () {
    return (
      <div className='global-nav-container'>
        <nav className={this.state.showMobileNav ? 'global-nav is-open' : 'global-nav'}>
          <h3>Menu</h3>
          <div className='close-button' onClick={this.toggleMenu}>
            <i className="material-icons">close</i>
          </div>
          <div className='nav-collection'>
            <div className='nav-item'>
              <a href="/about" className='animated-line'>About</a>
            </div>
            <div className='nav-item'>
              <a href="/about#legal" className='animated-line'>Legal Experience</a>
            </div>
            <div className='nav-item'>
              <a href="/about#tech" className='animated-line'>Tech Experience</a>
            </div>
          </div>
          <div className='nav-collection'>
            <div className='nav-item'>
              <a href="/services" className='animated-line'>Services</a>
            </div>
            <div className='nav-item'>
              <a href="/contact" className='animated-line'>Contact</a>
            </div>
            <div className='nav-item'>
              <a href="/blog" className='animated-line'>Blog</a>
            </div>
          </div>
        </nav>
        <div className='mobile-nav-button-container'>
          <div className='mobile-nav-button' onClick={this.toggleMenu}>
            <i className="material-icons">menu</i>
          </div>
        </div>
      </div>
    )
  }
})

const mapDispatchToProps = (dispatch) => ({
  setSearchTerm (searchTerm) {
    dispatch(setSearchTerm(searchTerm))
  },
  setCommandName (commandName) {
    dispatch(setCommandName(commandName))
  },
  setCommandHistory (commandName) {
    dispatch(setCommandHistory(commandName))
  },
  setMobileNav (mobileNavIsVisible) {
    dispatch(setMobileNav(mobileNavIsVisible))
  }
})

const mapStateToProps = (state) => ({
  searchTerm: state.searchTerm,
  commandHistory: state.commandHistory,
  mobileNavIsVisible: state.mobileNavIsVisible
})

export default connect(mapStateToProps, mapDispatchToProps)(Nav)



