const SET_SEARCH_TERM = 'SET_SEARCH_TERM'
const SET_COMMAND_NAME = 'SET_COMMAND_NAME'
const SET_COMMAND_HISTORY = 'SET_COMMAND_HISTORY'
const SET_MOBILE_NAV = 'SET_MOBILE_NAV'

const searchTerm = (state = '', action) => {
  switch (action.type) {
    case SET_SEARCH_TERM:
      return action.payload
    default:
      return state
  }
}

const commandName = (state = '', action) => {
  switch (action.type) {
    case SET_COMMAND_NAME:
      return action.payload
    default:
      return state
  }
}

const commandHistory = (state = [], action) => {
  switch (action.type) {
    case SET_COMMAND_HISTORY:
      return [...state, action.payload]
    default:
      return state
  }
}

const mobileNavIsVisible = (state = false, action) => {
  switch (action.type) {
    case SET_MOBILE_NAV:
      return action.payload
    default:
      return state
  }
}

export { searchTerm, commandName, commandHistory, mobileNavIsVisible }



